* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.container {
    width: 420px;
    height: 690px;
}

.display {
    background-color: black;
    color: pink;
    display: flex;
    height: 30px;
    padding: 5px 20px;
    font-size: 24px;
    font-weight:200;
    text-align: right;
    outline: 1px solid purple;
}

.btn-container {
    display: flex;
}

.row1 {
    display: flex;
    outline: 1px solid magenta;
    align-items: center;
    text-align: center;
}

