button {
    width: 100%;
    height: 45px;
    font-size: 20px;
    background-color: white;
}

button:hover {
    width: 100%;
    height: 45px;
    font-size: 20px;
    background-color: blueviolet;
    font-weight: bold;
    color: red;
    opacity: 0.9;
}

.operator {
    background-color: purple;
    color: white;
}

.clear{
background-color: burlywood;
color: white;
}

.neg {
background-color: darkorchid;
color: white;
}

.banana {
background-color:hotpink;
color: white;
}

.calculator{
    display: flex;
    align-items: center;
    width: 100%;
    height: auto;
    text-align: center;
    margin-top: 100px;
    justify-content:center;
    overflow: hidden;
}

.copyright {
    display:grid;
    flex: 1 0 auto;
    flex-shrink: 0;
    flex-direction: column;
    background-color: black;
    color: white;
    margin-bottom: -1px;
}